<template>
    <div>
        <el-date-picker
            v-model="startDate"
            v-bind="startInfo">
        </el-date-picker>
        <span class="ml10 mr10">{{connector}}</span>
        <el-date-picker
            v-model="endDate"
            v-bind="endInfo">
        </el-date-picker>
    </div>
</template>

<script>
export default {
    name: 'DateSearch',
    props: {
        start: {
            type: String
        },
        end: {
            type: String
        },
        startObj: {
            type: Object,
            default: () => ({})
        },
        endObj: {
            type: Object,
            default: () => ({})
        },
        connector: {
            type: String,
            default: '-'
        }
    },
    data () {
        return {
            startDate: '',
            endDate: ''
        }
    },
    computed: {
        startInfo () {
            return Object.assign({
                placeholder: '开始日期',
                type: 'date',
                'picker-options': this.pickerOptionsStart
            }, this.startObj)
        },
        endInfo () {
            return Object.assign({
                placeholder: '结束日期',
                type: 'date',
                'picker-options': this.pickerOptionsEnd
            }, this.endObj)
        },
        pickerOptionsStart () {
            return {
                disabledDate: (time) => {
                    let endDateVal = this.endDate
                    if (endDateVal) {
                        return time.getTime() > new Date(endDateVal).getTime()
                    }
                }
            }
        },
        pickerOptionsEnd () {
            return {
                disabledDate: (time) => {
                    let beginDateVal = this.startDate
                    if (beginDateVal) {
                        // todo: 这个部分需要试验一下，date formatter之后怎么改变
                        return time.getTime() < new Date(beginDateVal).getTime()
                    }
                }
            }
        }
    },
    watch: {
        startDate (val) {
            this.$emit('update:start', val)
        },
        endDate (val) {
            this.$emit('update:end', val)
        }
    }
}
</script>

